
.mainArea {
    
    width: 60%;
    background-color: #fff;
    margin-top: 1rem;
    margin-left: 2rem;
}
.mainArea .main-head {
    width: 100%;
    padding: 1rem 0;
    font-size: 1.5rem;
    background-color: #f6f6f6;

    display: -webkit-box;

    display: -ms-flexbox;

    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.mainArea .main-head ul {
    list-style: none;
    margin-left: 2rem;
}
.mainArea .main-head ul li {
    float: left;
    margin-right: 2rem;
}
.mainArea .main-head ul li a {
    height: 1.5rem;
    font-size: 1.5rem;
    text-decoration: none;
    color: #80bd01;
}
.mainArea .main-head ul li a:hover {
    color: #005580;
}
.selected {
    color: #fff!important;
    background-color: #80bd01!important;
}
.no-selected {
    color: #999999!important;
    background-color: #e5e5e5!important;
}
.mainArea .main-section {
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;
}
.bdrs {
    border-radius: 3px;
    padding: .2rem;
}
.mainArea .main-section .main-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 1rem;
    height: 4rem;
    position: relative;
    background-color: #fff;
    border-bottom: 1px solid #f6f6f6;
}
.mainArea .main-section .main-wrap:hover {
    background-color: #f6f6f6;
}
.mainArea .main-author {
    width: 3rem;
    height: 3rem;
    margin-right: .5rem;
}
.mainArea .main-author a {
    display: inline-block;
    height: 3rem;
}
.mainArea .main-author a img {

    height: 3rem;
}
.mainArea .main-number {
    color: #aaa;
    font-size: 1.5rem;
    margin-right: .5rem;
    width: 7rem;
    text-align: center;
}
.mainArea .main-forum {
    width: 3rem;
    font-size: 1.3rem;
    text-align: center;
    margin-right: .5rem;

    position: relative;
    left: 0rem;
}
.mainArea .main-wrap .main-title {
    width: 38rem;
    margin-right: .5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.mainArea .main-wrap .main-title a {
    color: #000;
    font-size: 1.6rem;
    text-decoration: none;
}
.mainArea .main-wrap .main-title a:hover {
    text-decoration: underline;
}
.mainArea .main-right {
    height: 2rem;
    /*display: flex;
    flex-flow: row nowrap;*/
}
.mainArea .main-lasttime {
    width: 5rem;
    text-align: right;
}



